.gpt3__navbar {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 1rem 6rem;
}

.gpt3__navbar-links {
    /* border: 1px solid #fff; */
    flex: 1;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    
}

.gpt3__navbar-link_logo {
    margin-right: 2rem;
}

.gpt3__navbar-link_logo img {
    width: 62px;
    height: 16px;
}

.gpt3__navbar-links_container {
    display: flex;
    flex-direction: row;
    
}

.gpt3__navbar-sign {
    display: flex;
    justify-content: flex-end;
    align-items: center;
}

.gpt3__navbar-links_container p,
.gpt3__navbar-sign p,
.gpt3__navbar-menu_container-links p {
    color: #fff;
    font-family: var(--font-family);
    font-weight: 500;
    font-size: 18px;
    line-height: 25px;
    text-transform: capitalize;
    margin: 0 1rem;
    cursor: pointer;
}

.gpt3__navbar-sign button,
.gpt3__navbar-menu_contaner button,
.gpt3__navbar-menu_container-links-sign button{
    padding: 0.7rem 2rem;
    color: #fff;
    font-family: var(--font-family);
    font-weight: 500;
    font-size: 18px;
    border: none;
    outline: none;
    cursor: pointer;
    background: #FF4820;
    border-radius: 5px;

}

.gpt3__navbar-menu{
    margin-left: 1rem;
    position: relative;
    display: none;
}

.gpt3__navbar-menu svg{
    cursor: pointer;
}
.gpt3__navbar-menu_container{
    /* border: 1px solid #fff; */
    display: flex;
    justify-content: flex-end;
    flex-direction: column;
    align-items: flex-end;
    text-align: end;
    background: var(--color-footer);
    padding: 2rem;
    position: absolute;
    top: 40px;
    right: 0;
    margin-top: 1rem;
    min-width: 210px;
    border-radius: 5px;
    box-shadow: 0 0 5 rgba(0,0,0,0.2);
}
.gpt3__navbar-menu_container p{
    margin: 1rem 0;
}

.gpt3__navbar-menu_container-links-sign{
    display: none;
}

@media screen and (max-width:1050px) {
    .gpt3__navbar-links_container{
        display: none;
    }
    .gpt3__navbar-menu{
        display: block;
    }

}

@media screen and (max-width:700px) {
    .gpt3__navbar{
        padding: 2rem 4rem;
    }
}

@media screen and (max-width:550px) {
    .gpt3__navbar{
        padding: 2rem;
    }
    .gpt3__navbar-sign{
        display: none;
    }
    .gpt3__navbar-menu_container-links-sign{
        display: block;
    }
}
