.gpt3__header {
    display: flex;
} 

.gpt3__header-content {
    flex: 1;
    display: flex;
    justify-content: center;
    align-items: flex-start;
    flex-direction: column;
    /* margin-right: 5rem; */
    /* border: 1px solid #fff; */
}

.gpt3__header-content h1 {
    font-family: var(--font-family);
    font-weight: 800;
    font-size: 62px;
    line-height: 75px;
}

.gpt3__header-content p {
    font-family: var(--font-family);
    font-weight: 400;
    font-size: 20px;
    line-height: 27px;
    color: var(--color-text);
    margin-top: 1.5rem;
}

.gpt3__header-content__input {
    width: 100%;
    margin: 2rem 0 1rem;
    display: flex;
}

.gpt3__header-content__input input {
    background: var(--color-footer);
    border-radius: 5px 0px 0px 5px;
    color: var(--color-text);
    flex: 2;
    width: 100%;
    min-height: 50px;
    border: 2px solid var(--color-footer);
    padding: 0 1rem;
    outline: none;
    color: #fff;

}

.gpt3__header-content__input button {
    background: #FF4820;
    flex: 0.6;
    width: 100%;
    min-height: 50px;
    border-radius: 0px 5px 5px 0px;
    border: none;
    outline: none;
    color: #fff;
    cursor: pointer;
    font-size: 20px;
    font-weight: bold;
    padding: 0 1rem;

}

.gpt3__header-content__people{
    width: 100%;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    margin-top: 2rem;
}
.gpt3__header-content__people img{
    width: 181.79px;
    height: 38px;
}

.gpt3__header-content__people p{
    margin-left: 1rem;
    font-weight: 500;
    font-size: 12px;
    color: #fff;
    text-align: center;
}

.gpt3__header-image{
    flex: 1;
    display: flex;
    justify-content: center;
    align-items: center;
}

.gpt3__header-image img{
    width: 100%;
    height: 100%;
}

@media screen and (max-width:1050px) {
    .gpt3__header{
      flex-direction: column;

    }
    .gpt3__navbar-content{
       margin: 0 0 3rem;
       margin-right: 0rem;
    }
   

}
@media screen and (max-width:650px) {
    .gpt3__header h1{
        font-size: 28px;
        line-height: 60px;
    }
    .gpt3__header p{
        font-size: 16px;
        line-height: 24px;
    }
    .gpt3__header-content__people{
        flex-direction: column;
    }
    .gpt3__header-content__people p{
        margin: 0;
    }

    .gpt3__header-content__input input,
    .gpt3__header-content__input button{
        font-size: 16px;
        line-height: 24px;
    }

}

@media screen and (max-width:490px) {
    .gpt3__header h1{
        font-size: 26px;
        line-height: 48px;
    }
    .gpt3__header p{
        font-size: 14px;
        line-height: 24px;
    }
    .gpt3__header-content__input input,
    .gpt3__header-content__input button{
        font-size: 12px;
        line-height: 16px;
    }

}